/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

/* Extra resetting 
--------------------*/
/* Makes all images responsive */
img {
  max-width: 100%;
  height: auto; }

/* apply a natural box layout model to all elements, but allowing components to change */
html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

html, body {
  background-color: black;
  font-size: 14px;
  color: #ffffff;
  font-family: 'Open Sans', Arial, sans-serif; }

.top-header {
  height: 450px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  @media (min-width: 600px) {
    .top-header {
      flex-direction: row; } }
  .top-header .logo {
    text-align: center;
    margin-top: 150px;
    margin-bottom: 20px; }
    @media (min-width: 600px) {
      .top-header .logo {
        width: 170px; } }
  .top-header .logo-on-click {
    margin-top: 0;
    width: 150px; }
    @media (min-width: 600px) {
      .top-header .logo-on-click {
        width: 80px;
        margin-left: 80px; } }
  @media (min-width: 600px) {
    .top-header .choose-section-wrapper {
      margin-top: 120px; } }
  @media (min-width: 600px) {
    .top-header .choose-section-wrapper-on-click {
      margin-top: -20px;
      margin-left: 90px; } }
  .top-header p {
    font-size: 12px;
    text-align: center; }
    @media (min-width: 600px) {
      .top-header p {
        font-size: 14px; } }
  .top-header .choose-section {
    margin-top: 15px;
    margin-bottom: 100px; }
    @media (min-width: 600px) {
      .top-header .choose-section {
        margin-bottom: 0; } }
    .top-header .choose-section .select-section {
      font-size: 17px; }
  .top-header .choose-section-on-click {
    margin-bottom: 0; }

.top-header-on-click {
  height: 300px; }
  @media (min-width: 600px) {
    .top-header-on-click {
      height: 150px;
      justify-content: flex-start; } }

.articles .grid-container {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(12, 1fr); }
  .articles .grid-container li a {
    text-decoration: none; }
    .articles .grid-container li a div {
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      height: 400px;
      width: 400px;
      display: flex;
      flex-direction: column;
      justify-content: flex-end; }
      .articles .grid-container li a div p {
        line-height: 1.5rem;
        color: #ffffff;
        font-size: 15px;
        padding: 0.5rem;
        background: rgba(0, 0, 0, 0.6); }

.copyright {
  text-align: center;
  margin-top: 180px;
  margin-bottom: 100px;
  color: #c2c2c2; }

.copyright-on-click {
  margin-top: 20px;
  margin-bottom: 50px; }
